/**
 * Base: Buttons
 */
.button {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  text-decoration: none;
  font-weight: 700;
  font-family: "Lato", "Helvetica", "Arial", "sans-serif";
  -webkit-transition: background 0.2s;
  -o-transition: background 0.2s;
  transition: background 0.2s;
  padding: 7px 80px;
  color: #000000;
  background: #c87832;
  border: 1px solid transparent;
}
.button:focus,
.button:active:focus,
.button.active:focus,
.button.focus,
.button:active.focus,
.button.active.focus {
  outline: none;
}
.button:active,
.button.active {
  outline: 0;
}
.button.disabled,
.button[disabled] {
  cursor: disabled;
}
a.button {
  text-decoration: none;
}
a.button.disabled,
fieldset[disabled] a.button {
  pointer-events: none;
}
.button:hover,
.button:focus,
.button.focus {
  color: #000000;
  background: #9f6028;
  text-decoration: none;
}
.button--secondary {
  color: #ffffff;
  background: #c87832;
}
.button--secondary:hover,
.button--secondary:focus,
.button--secondary.focus {
  color: #ffffff;
  background: #9f6028;
}
.button--ghost {
  color: #a8c23b;
  background: transparent;
  border-color: #a8c23b;
}
