}
.site-navigation__counter {
  border-radius: 100%;
  position: absolute;
  top: 0;
  right: -18px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  background-color: #e32826;
  font-size: 10px;
  font-weight: bold;
  z-index: 1;
  -webkit-transition: all 0.3s;
