/**
 * Component: Site header
 * Logo, menu, sticky feature
 */

 .site-header {
   position: relative;
   z-index: 1000;
   top: 0 !important;
   left: 0;
   width: 100%;
   height: 85px;

   //background-color: rgba(255, 255, 255, .9);
   background-color: @color-white;
   //border-bottom: 4px solid @color-secondary;
   padding: 10px 0;
   .transition(padding-left .3s);

   @media (min-width:@screen-sm) {
     height: 150px;
   }
  //@media (max-width:@screen-sm-max) {position: fixed;}

  &.js-fixed {
    position: fixed;
    top: 0;

    .site-navigation {margin-top: 0;}
    .site-navigation__counter {
      top: -8px;
      width: 20px;
      height: 20px;
      line-height: 20px;
      font-size: 8px;
    }
    .logo__image {
      width: 58px;
      height: 18px;
    }
    .logo__branch {font-size: 15px;}
  }

  @media (min-width:@screen-md) {
    padding: 10px 0 0;
    top: 42px; // 42px off from the global header
  }

  //@media (max-width:420px) {
  //  &.js-menu-open {padding-left: 50%;}
  //}
}

.logo {
  display: block;
  text-decoration: none;
  position: relative;
  top: 10px;
  left: 10px;
  float: left;

  &:hover {text-decoration: none;}

  @media (min-width:@screen-sm) {
    top: 30px;
    margin-left: 20px;
  }

  @media (min-width:@screen-md) {
    top: 30px;
    margin-left: 80px;
  }

  &__image {
    display: inline-block;
    width: 120px;
    height: 45px;
    .transition(all .3s);

    @media (min-width:@screen-sm) {
      width: 195px;
      height: 75px;
    }

    @media (min-width:@screen-md) {
      width: 195px;
      height: 75px;
    }
  }

  &__branch {
    display: inline-block;
    color: @color-text;
    line-height: 1;
    font-size: 15px;
    .transition(all .3s);

    @media (min-width:@screen-md) {font-size: 30px;}
  }

  // Mobile variant, for the mobile navigation
  &--mobile {
    margin-bottom: 10px;
    float: none;
    top: 0;

    .logo__image {width: 76px;}
    .logo__branch {font-size: 22px;}
  }
}

.header-right {
  position: relative;
  float: right;
  margin-top: 20px;
  margin-right: 10px;

  @media (min-width:@screen-md) {
    margin-right: -60px;
    margin-top: 0;
    margin-right: 0;
  }
}

.social-media {
  //width: 400px;
  //position: relative;
  //float: right;
  display: none;

  @media (min-width:@screen-sm) {
    display: block;
    margin-right: 215px;
  }

  @media (min-width:@screen-md) {
    margin-right: 271px;
    margin-top: 25px;
  }

  &__hashtag {
    display: inline-block;
    font-size: 20px;
  }

  &__link {
    text-decoration: none;
  }

  &__instagram {
    width: 25px;
    height: 25px;
    fill: @color-black;
    position: relative;
    top: 5px;
    margin-left: 45px;

    @media (min-width:@screen-sm) {
      margin-left: 10px;
    }

    @media (min-width:@screen-md) {
      margin-left: 45px;
    }
  }

  &__facebook {
    width: 25px;
    height: 25px;
    fill: @color-black;
    position: relative;
    top: 5px;
    margin-left: 10px;
  }
}


